import specialBanner from '@/components/EtherealWheat-banner/specialBanner.vue'
export default {
	components: {
		specialBanner
	},
	data() {
		return {
			bannerList: [{
				picture: 'http://oss.cswendu.com/app/wx0.0.1/word-list-select-4j.png',
				title: '四级英语',
				path: 'siji',
				word_num:'666'
			}, {
				picture: 'http://oss.cswendu.com/app/wx0.0.1/word-list-select-6j.png',
				title: '六级英语',
				path: 'liuji',
				word_num:'666'
			}, {
				picture: 'http://oss.cswendu.com/app/wx0.0.1/word-list-select-ky.png',
				title: '考研英语',
				path: 'kaoyan',
				word_num:'666'
			}],
			swiperConfig: {
				indicatorDots: true,
				indicatorColor: 'rgba(255, 255, 255, .4)',
				indicatorActiveColor: 'rgba(255, 255, 255, 1)',
				autoplay: false,
				interval: 3000,
				duration: 300,
				circular: true,
				previousMargin: '58rpx',
				nextMargin: '58rpx'
			},
			default_index:0
		};
	},
	onLoad() {
		this.get_word_num()
	},
	methods:{
		select_change:function(e){
			// console.log(e)
			this.default_index = e
			console.log(this.default_index)
		},
		get_word_num:function(){
			//4j
			let data1 = {
				user_token:this.$store.getters.common_token,
				app_class:'wechat',
				which_dict:'siji',
				read_model:'read_num@1',
				read_order:'word_asc',
				page:1,
			}
			this.$api.word_list(data1).then(res=>{
				// console.log(res)
				this.bannerList[0].word_num = res.data.paging.total
			})
			//6j
			let data2 = {
				user_token:this.$store.getters.common_token,
				app_class:'wechat',
				which_dict:'liuji',
				read_model:'read_num@1',
				read_order:'word_asc',
				page:1,
			}
			this.$api.word_list(data2).then(res=>{
				// console.log(res)
				this.bannerList[1].word_num = res.data.paging.total
			})
			//ky
			let data3 = {
				user_token:this.$store.getters.common_token,
				app_class:'wechat',
				which_dict:'kaoyan',
				read_model:'read_num@1',
				read_order:'word_asc',
				page:1,
			}
			this.$api.word_list(data3).then(res=>{
				// console.log(res)
				this.bannerList[2].word_num = res.data.paging.total
			})
		},
		type_of_word:function(){
			let type = this.bannerList[this.default_index].path
			this.$store.dispatch('Word/dictionary',type)
			
			if(this.$store.state.Word.read_model==''||this.$store.state.Word.read_order==''){
				uni.showModal({
				    title: '提示',
				    content: '请先进行单词设置',
				    success: function (res) {
				        if (res.confirm) {
				            // console.log('用户点击确定');
							uni.navigateTo({
								url: '/pages/word_list_setting/word_list_setting'
							});
				        } else if (res.cancel) {
				            // console.log('用户点击取消');
							// uni.switchTab({
							//     url: '/pages/word_list_index/word_list_index'
							// });
				        }
				    }
				});
			}else{
				uni.switchTab({
				    url: '/pages/word_list_index/word_list_index'
				});
			}
			
		}
	}
}
